#skills{
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
}

.skillTitle{
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
}

.skillDesc{
    font-weight: 200;
    font-size: 1rem;
    max-width: 50rem;
    padding: 0 2rem;
}

.skillBars{
    margin: 1.5rem;
    width: 100vw;
    max-width: 80%;
    text-align: left;
}

.skillBar{
    display: flex;
    margin: 1rem;
    padding: 1.5rem 1.5rem;
    background: rgb(50,50,50);
    border-radius: 0.5rem;
}


.lists{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.skillBarImg{
    object-fit: cover;
    width: 6rem;
    height: auto;
    margin-right: 3rem;
}

li{ 
    list-style-type: none;
    display: flex;
    margin: 2rem;
}